  border-radius: 8px !important;
}
.filepond--drop-label {
  background-color: white !important;
}
.filepond--panel {
  background-color: white !important;
}
.filepond--drip {
  background-color: rgba(59, 130, 246, 0.1) !important;
}
.diff-unchanged {
  background: transparent;
  color: #333333;
}
.diff-modified {
  background: #FFF3CD;
  border-left: 4px solid #FFE066;
  color: #856404;
}
.diff-added {
  background: #D4E6D4;
  border-left: 4px solid #28A745;
  color: #155724;
}
.diff-removed {
  background: #F8D7DA;
  border-left: 4px solid #DC3545;
  color: #721C24;
}
.diff-not-set {
  background: #F8F9FA;
  color: #6C757D;
  font-style: italic;
}
.field-highlight {
  margin: 0 -16px;
  padding: 8px 16px;
  border-radius: 4px;
}
.expansion-indicator {
  cursor: pointer;
  user-select: none;
  transition: transform 0.2s ease;
}
.expansion-indicator.expanded {
  transform: rotate(90deg);
}
.expansion-indicator:hover {
  color: #374151;
}
.nested-connector {
  border-left: 1px solid #dee2e6;
  margin-left: 10px;
  padding-left: 10px;
}
.field-indent-0 {
  margin-left: 0;
}
.field-indent-1 {
  margin-left: 20px;
}
.field-indent-2 {
  margin-left: 40px;
}
.field-indent-3 {
  margin-left: 60px;
}
.array-field .array-header {
  position: relative;
}
.array-field .array-items {
  border-left: 2px solid #e5e7eb;
  padding-left: 12px;
  margin-left: 8px;
}
.array-content {
  overflow-y: visible;
  margin-top: 4px;
  min-height: fit-content;
  max-height: none;
}
.array-item {
  display: flex;
  align-items: flex-start;
  gap: calc(var(--spacing) * 2);
  padding-block: calc(var(--spacing) * 1);
  border-left: 2px solid #e5e7eb;
  padding-left: 12px;
  margin-bottom: 4px;
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.025);
  font-family: ui-sans-serif, system-ui;
  word-break: break-word;
  overflow-wrap: break-word;
  line-height: 1.4;
}
.item-index {
  font-family: var(--font-roboto-mono);
  font-size: var(--text-xs);
  line-height: var(--tw-leading, var(--text-xs--line-height));
  color: var(--color-gray-500);
  min-width: 32px;
  text-align: right;
  flex-shrink: 0;
}
.item-content {
  flex: 1;
  font-size: var(--text-sm);
  line-height: var(--tw-leading, var(--text-sm--line-height));
  word-wrap: break-word;
  white-space: pre-wrap;
}
.field-value-container {
  display: block;
  min-height: 32px;
  padding: 8px 12px;
  border-radius: 4px;
  height: auto;
  max-height: none;
}
.field-label {
  display: block;
  font-size: 12px;
  font-weight: 600;
  color: #374151;
  margin-bottom: 4px;
  text-transform: uppercase;
  letter-spacing: 0.025em;
}
.field-value {
  display: block;
  font-size: 14px;
  line-height: 1.625;
  word-wrap: break-word;
  overflow-wrap: break-word;
  white-space: pre-wrap;
  hyphens: auto;
  max-width: 100%;
  font-family: ui-sans-serif, system-ui;
}
@media (max-width: 1024px) {
