  display: flex;
  flex-direction: column;
}
.expansion-indicator:focus {
  outline: 2px solid #3b82f6;
  outline-offset: 2px;
  border-radius: 2px;
}
.change-indicator {
  font-weight: 500;
  background: rgba(59, 130, 246, 0.1);
  padding: 2px 6px;
  border-radius: 12px;
